import { Component, OnDestroy, OnInit, OnChanges } from '@angular/core';
import { DatasourceService } from './shared/services/datasource.service';
import { Route, Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {

  isUserLoggedIn: boolean;
  appName: string = 'On Boarding App';
  adminName: string;

  constructor(private readonly datasourceService: DatasourceService, private readonly router: Router) {
    this.adminName = '';
    this.isUserLoggedIn = false;
    console.log('s' + this.isUserLoggedIn)
  }

  ngOnInit() {
    this.datasourceService.getAdminUser().subscribe((value) => {
      if (value) {
        this.adminName = value.name;
      }else{
        this.adminName = null;
      }
    })
    if (JSON.parse(localStorage.getItem('studentList'))) {
      console.log('context loaded again')
      this.datasourceService.setStudentList(JSON.parse(localStorage.getItem('studentList')));
    }
    if (JSON.parse(localStorage.getItem('adminUser'))) {
      console.log(this.adminName);
      this.adminName = JSON.parse(localStorage.getItem('adminUser')).name;
    }

    this.datasourceService.getIsUserLoggedIn().subscribe((value) => {
      this.isUserLoggedIn = value;
    })
  }

  ngOnDestroy() {
    localStorage.removeItem('adminUser');
  }

  logout() {
    localStorage.removeItem('adminUser');
    this.datasourceService.setAdminUser(null);
    this.router.navigate(['/adminLogin']);
  }

}
